export const LOGIN_TELLER = 'LOGIN_TELLER';
export const LOGOUT_TELLER = 'LOGOUT_TELLER';

export function loginTeller(teller){
  return {
    type: LOGIN_TELLER,
    payload: {
      teller: teller
    }
  };
}

export function logoutTeller(){
  return {
    type: LOGOUT_TELLER,
    payload: true
  };
}
